/** Better Formatting Redux plugin **/
.bf-toolbar .bf-arrow {
  transform: translate(2px, -13px) !important;
}

.bf-toolbar.bf-visible .bf-arrow,
.bf-toolbar.bf-hover:hover .bf-arrow {
  transform: translate(2px, -13px) rotate(-90deg) !important;
}

/** BetterVolume plugin **/
