/** Magane plugin **/
div#magane div.stickerWindow {
  background-color: var(--popout-color) !important;
  border-radius: var(--popout-radius) !important;
  box-shadow: var(--shadow-3dp) !important;
  margin-right: 5px;
  animation: bottom-pop-out 400ms var(--default-animation) !important;
}
div#magane div.stickerWindow div.packs-toolbar.has-scroll-x {
  border-radius: 0 0 var(--popout-radius) var(--popout-radius) !important;
}
div#magane div.stickerWindow div.packs-toolbar.has-scroll-y {
  border-radius: var(--popout-radius) 0 0 var(--popout-radius) !important;
}
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack div.icon-plus,
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack div.icon-favorite {
  width: 100%;
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-size: 75%;
  background-position: center !important;
}
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack div.icon-plus {
  background-image: url("https://discord.com/assets/28a6206f93399999d1a908d5c45232ad.svg");
  /* stylelint-disable-next-line scss/no-global-function-names */
  filter: grayscale(100%) brightness(125%) !important;
}
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack div.icon-favorite {
  background-image: url("https://discord.com/assets/430e873bc74e81778ccf63f5d440f0ca.svg");
  /* stylelint-disable-next-line scss/no-global-function-names */
  filter: invert(70%) grayscale(100%) brightness(130%) !important;
}
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack:hover div.icon-plus {
  /* stylelint-disable-next-line scss/no-global-function-names */
  filter: grayscale(0%) brightness(100%) !important;
}
div#magane div.stickerWindow div.packs-toolbar div.packs div.pack:hover div.icon-favorite {
  /* stylelint-disable-next-line scss/no-global-function-names */
  filter: invert(0%) grayscale(0%) brightness(100%) !important;
}
div#magane div.stickerWindow div.stickers div.pack span {
  font-weight: bold !important;
}
div#magane div.stickerWindow div.stickers div.pack div.sticker div.addFavorite,
div#magane div.stickerWindow div.stickers div.pack div.sticker div.deleteFavorite {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  top: unset !important;
  right: 3px !important;
  bottom: 3px !important;
  transform: none !important;
  opacity: 0.75;
}
div#magane div.stickerWindow div.stickers div.pack div.sticker div.addFavorite:hover,
div#magane div.stickerWindow div.stickers div.pack div.sticker div.deleteFavorite:hover {
  transform: scale(1.25) !important;
  opacity: 1;
}
div#magane div.stickerWindow div.stickers div.pack div.sticker div.addFavorite svg,
div#magane div.stickerWindow div.stickers div.pack div.sticker div.deleteFavorite svg {
  display: none;
}
div#magane div.stickerWindow div.stickers div.pack div.sticker div.addFavorite {
  background-image: url("https://discord.com/assets/0483f2b648dcc986d01385062052ae1c.svg");
}
div#magane div.stickerWindow div.stickers div.pack div.sticker div.deleteFavorite {
  background-image: url("https://discord.com/assets/fda7477c4eea759cf5407472387453bf.svg");
}
div#magane div.stickerWindow a {
  color: hsl(var(--accent-hsl)) !important;
}
div#magane .button {
  font-weight: bold !important;
}
div#magane ::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}
div#magane ::-webkit-scrollbar-thumb {
  border-radius: var(--popout-radius) !important;
}

/* stylelint-disable-next-line selector-id-pattern */
div#maganeButton {
  top: -1px;
  position: relative;
  background-image: url("https://discord.com/assets/4c1490be77de62e7cf3e5bb5e68dfff8.svg");
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: center;
  width: 36px;
  height: 44px;
  /* stylelint-disable-next-line scss/no-global-function-names */
  filter: invert(70%) grayscale(100%) brightness(130%);
}
div#maganeButton:hover {
  filter: grayscale(0%);
}
div#maganeButton img {
  display: none;
}

